window.dom = {}

//ÂàõÂª∫ÂÖÉÁ¥†ÂáΩÊï∞
dom.create = (string) => {
    const container = document.createElement(`template`)
    container.innerHTML = string.trim()  //trimÁî®‰∫éÂøΩÁï•Á©∫Ê†ºÊç¢Ë°åÁ≠âÊñáÊú¨
    return container.content.firstChild  //templateÂøÖÈ°ªÁî®content.firstChildËé∑Âèñ
}

//ÂàõÂª∫ÂºüÂºüÂÖÉÁ¥†ÂáΩÊï∞
dom.after = (node,node2) => {
    node.parentNode.insertBefore(node2, node.nextSibling)
}

//ÂàõÂª∫Âì•Âì•ÂÖÉÁ¥†ÂáΩÊï∞
dom.before = (node,node2) => {
    node.parentNode.insertBefore(node2, node)
}

//ÂàõÂª∫Â≠êÂÖÉÁ¥†ÂáΩÊï∞
dom.append = (parent, node) => {
    parent.appendChild(node)
}

//ÂàõÂª∫Áà∂ÂÖÉÁ¥†ÂáΩÊï∞
dom.wrap = (node, parent) => {
    dom.before(node, parent)
    dom.append(parent, node)
}

//Âà†Èô§ËäÇÁÇπ
dom.remove = (node) => {
    node.parentNode.removeChild(node)
    return node
}

//Âà†Èô§Âêé‰ª£ÔºàÊ∏ÖÁ©∫ËäÇÁÇπÂÜÖÈÉ®Ôºâ
dom.empty = (node) => {
    const array = []
    let x = node.firstChild
    while(x){
        array.push(dom.remove(x))
        x = node.firstChild
    }
    return array
}

//ÊîπÊàñËÄÖÊü•‰∏Ä‰∏™Â±ûÊÄß
dom.attr = function (node, name, value) {  //ÈáçËΩΩ
    if (arguments.length === 3){
        node.setAttribute(name, value)
    }else if (arguments.length === 2){
        return node.getAttribute(name)
    }
}

//ÊîπÊàñËÄÖÊü•ÊñáÊú¨ÂÜÖÂÆπ
dom.text = function (node, string) {  //ÈÄÇÈÖç
    if (arguments.length === 2){
        if (`innerText` in node){
            node.innerText = string
        }else{
            node.textContent = string
        }
    }else if (arguments.length === 1){
        if (`innerText` in node){
            return node.innerText
        }else{
            return node.textContent
        }
    }
}

//ÊîπÊàñËÄÖÊü•HTMLÂÜÖÂÆπ
dom.html = function (node, string) {
    if (arguments.length === 2){
        node.innerHTML = string
    }else if (arguments.length ===1){
        return node.innerHTML
    }
}

//ÊîπÊàñËÄÖÊü•styleÂÜÖÂÆπ
dom.style = function (node, name, value) {
    if (arguments.length === 3){
        node.style[name] = value
    }else if (arguments.length === 2) {
        if (typeof name === `string`){
            return node.style[name]
        }else if (name instanceof Object){
            for (let key in name){
                node.style[key] = name[key]
            }
        }
    }
}

//Ê∑ªÂä†„ÄÅÁßªÈô§„ÄÅÊ£ÄÊü• class
dom.class = {
    add(node, className) {
        if (arguments.length === 2) {
            node.classList.add(className)
        }else if (arguments.length > 2){
            for (let i = 1; i<arguments.length; i++){
                node.classList.add(arguments[i])
            }
        }
    },
    remove(node, className) {
        node.classList.remove(className)
    },
    check(node, className) {
        return node.classList.contains(className)
    },
}

//Ê∑ªÂä†‰∫ã‰ª∂
dom.on = function (node, eventName, fn) {
    node.addEventListener(eventName, fn)
}

//ÁßªÈô§‰∫ã‰ª∂
dom.off = function (node, eventName, fn) {
    node.removeEventListener(eventName, fn)
}

//Êü•ËäÇÁÇπÁöÑÂÜÖÂÆπ
dom.find = function (selector, scope) {
    return (scope || document).querySelectorAll(selector)
}

//Êü•ËäÇÁÇπÁöÑÁà∂ÂÖÉÁ¥†
dom.parent = function (node) {
    return node.parentNode
}

//Êü•ËäÇÁÇπÁöÑÂ≠êÂÖÉÁ¥†
dom.children = function (node) {
    return node.children
}

//Êü•ËäÇÁÇπÁöÑÂÖÑÂºüÂßêÂ¶πÂÖÉÁ¥†
dom.siblings = function (node) {
    return Array.from(node.parentNode.children).filter(n=>n!==node)
}

//Êü•ËäÇÁÇπÁöÑÂºüÂºü
dom.next = function (node) {
    let x = node.nextSibling
    while(x && x.nodeType === 3){
        x = x.nextSibling
    }
    return x
}

//Êü•ËäÇÁÇπÁöÑÂì•Âì•
dom.previous = function (node) {
    let x = node.previousSibling
    while(x && x.nodeType === 3){
        x = x.previousSibling
    }
    return x
}

//ÈÅçÂéÜÂ≠êÂÖÉÁ¥†
dom.each = function (nodeList, fn) {
    if (arguments.length === 1) {
        return dom.children(nodeList)
    }else if (arguments.length === 2) {
        for (let i = 0; i < nodeList.length; i++){
            fn.call(null, nodeList[i])
        }
    }
}

//ËäÇÁÇπÂú®ÂÖÑÂºüÂßê‰ª¨‰∏≠ÊéíË°åÁ¨¨Âá†
dom.indexOf = function (node) {
    let a = dom.parent(dom.find(node)[0]).children
    let i = 0
    for (i = 0; i<a.length;i++){
        if (dom.find(node)[0] === a[i]){
            break;
        }
    }
    return i+1
}